import ProfileMenu from "../../components/ProfileMenu";
import { useTranslation } from "react-i18next";
import { useState } from "react";
import { Link } from "react-router-dom";
export default function Profile (){
    const [male, setMale] = useState(1);
    
    // Подгрузка male
    const { t, i18n } = useTranslation();
    return(<>
    <div className="Profile-Block">
        <ProfileMenu url={'/profile/main'}/>
        <div className="Profile-Block__container">
            <div className="Mobile-Selector">
                <Link to='/profile/main' className="selected">{t('main')}</Link>
                <Link to='/profile/verification'>{t('verification')}</Link>
           
                <Link to='/profile/safety'>{t('safety')}</Link>
           
            </div>
            <span> <p>{t('profile')}</p>  <svg width="10" height="10" viewBox="0 0 10 10" fill="none" xmlns="http://www.w3.org/2000/svg">
<path d="M2.5 7.08329L4.58333 4.99996L2.5 2.91663M5.41667 7.08329L7.5 4.99996L5.41667 2.91663" stroke="white" stroke-opacity="0.3" stroke-linecap="round" stroke-linejoin="round"/>
</svg>
<p>{t('main')}</p>
</span>
<h1>{t('main')}</h1>
        <div className="Profile-Block__fields">
            <div>
                <nav>
                <p>E-mail</p>
                <input type="text" placeholder={t('enter_your_email')} />
                </nav>
                <nav>
                <p>{t('country')}</p>
                <div className="selector">
                    <div onClick={(e)=>{e.preventDefault();e.target.parentElement.classList.toggle('open')
               
                }} className="selector__header">
                        <p className="selector__value">
                        <svg width="15" height="15" viewBox="0 0 15 15" fill="none" xmlns="http://www.w3.org/2000/svg">
<rect width="15" height="15" rx="3" fill="#000AFF"/>
<path d="M0 2.5C0 1.11929 1.11929 0 2.5 0H12.5C13.8807 0 15 1.11929 15 2.5V5H0V2.5Z" fill="white"/>
<path d="M0 10H15V12.5C15 13.8807 13.8807 15 12.5 15H2C0.89543 15 0 14.1046 0 13V10Z" fill="#FF0000"/>
</svg>


                            Россия</p>
                        <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
<path d="M6 9L12 15L18 9" stroke="#7762CD" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/>
</svg>

                    </div>
                    <div onClick={(e)=>{
                        if (e.target.classList.contains('selector__body'))return;
                        e.target.parentElement.previousSibling.querySelector('p').innerHTML = e.target.innerHTML;
                        e.target.parentElement.parentElement.classList.remove('open');
                        
                    }} className="selector__body">
                        <div>
                        <svg width="15" height="15" viewBox="0 0 15 15" fill="none" xmlns="http://www.w3.org/2000/svg">
<rect width="15" height="15" rx="3" fill="#000AFF"/>
<path d="M0 2.5C0 1.11929 1.11929 0 2.5 0H12.5C13.8807 0 15 1.11929 15 2.5V5H0V2.5Z" fill="white"/>
<path d="M0 10H15V12.5C15 13.8807 13.8807 15 12.5 15H2C0.89543 15 0 14.1046 0 13V10Z" fill="#FF0000"/>
</svg>


                            Россия
     
                        </div>
                        <div>
                        <svg width="15" height="15" viewBox="0 0 15 15" fill="none" xmlns="http://www.w3.org/2000/svg">
<rect width="15" height="15" rx="3" fill="#000AFF"/>
<path d="M0 2.5C0 1.11929 1.11929 0 2.5 0H12.5C13.8807 0 15 1.11929 15 2.5V5H0V2.5Z" fill="white"/>
<path d="M0 10H15V12.5C15 13.8807 13.8807 15 12.5 15H2C0.89543 15 0 14.1046 0 13V10Z" fill="#FF0000"/>
</svg>


                            Россия
     
                        </div>
                        <div>
                        <svg width="15" height="15" viewBox="0 0 15 15" fill="none" xmlns="http://www.w3.org/2000/svg">
<rect width="15" height="15" rx="3" fill="#000AFF"/>
<path d="M0 2.5C0 1.11929 1.11929 0 2.5 0H12.5C13.8807 0 15 1.11929 15 2.5V5H0V2.5Z" fill="white"/>
<path d="M0 10H15V12.5C15 13.8807 13.8807 15 12.5 15H2C0.89543 15 0 14.1046 0 13V10Z" fill="#FF0000"/>
</svg>


                            Россия
     
                        </div>
                    </div>
                </div>
                
                </nav>
            </div>
            <div>
            <nav>
                <p>{t('username')}</p>
                <input type="text" defaultValue="Username" />
                </nav>
                <nav>
                <p>{t('city')}</p>
                <input type="text" placeholder={t('enter_your_city')} />
                
                </nav>
            </div>
            <div>
            <nav>
                <p>{t('name')}</p>
                <input type="text" placeholder={t('enter_your_name')} />
                </nav>
                <nav>
                <p>{t('address')}</p>
                <input type="text" placeholder={t('enter_your_address')} />
                
                </nav>
            </div>
            <div>
            <nav>
                <p>{t('last_name')}</p>
                <input type="text" placeholder={t('enter_your_last_name')} />
                </nav>
                <nav>
                <p>{t('post_index')}</p>
                <input type="text" placeholder={t('enter_your_post_index')} />
                
                </nav>
            </div>
            <div>
            <nav>
                <p>{t('birth_date')}</p>
                <input type="text" placeholder="DD.MM.YYYY" />
                </nav>
                <nav>
                <p>{t('mobile_phone')}</p>
                <input type="text" placeholder="+79999999999" />
                
                </nav>
            </div>
            <div>
            <nav>
                <p>{t('gender')}</p>
                <div className="male-selector">
<label onClick={()=>{setMale(1)}}>
<input type="checkbox" checked={male==1} name="man"  />
<p>{t('gender_male')}</p>
</label>
<label onClick={()=>{setMale(2)}}>
    <input type="checkbox"  checked={male==2} name="woman"  />
    <p>{t('gender_female')}</p>
</label>
                </div>
                </nav>
                <nav>
                <p>Автоактивация бонусов</p>
              <label  className="toggle-switch">
                <input type="checkbox" name="bonus-auto-activation"  />
<span></span>
              </label>
                </nav>
            </div>
            <a className="btn">{t('save')}</a>
        </div>
        </div>
    </div>
    </>)
}